<template>
  <div>
    <Breadcrumb
    class="mb-8 mt-4"
    pageTitle="Add Customer"
    :backTo="{ name: 'layoutCustomer' }"
    :items="breadcrumb">
      <template #right>
        <Button color="teal" :loading="loading" @click="createCustomer">Save</Button>
      </template>
    </Breadcrumb>
    <Card>
      <CustomerForm v-model:data="data" ref="refForm" />
    </Card>
  </div>
</template>

<script>
import { defineComponent, ref} from 'vue'
import { useRouter } from 'vue-router'
import { useCreate } from 'composable/customer'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import CustomerForm from 'components/Customer/Form.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Button,
    Card,
    CustomerForm,
    Breadcrumb
  },
  setup() {
    const router = useRouter()
    const refForm = ref(null)
    const { data, loading, action } = useCreate()
    const createCustomer = async() => {
      if(!refForm.value.validate || (refForm.value.validate && refForm.value.validate())){
        await action()
        ElMessage({
          message: 'Data has been saved!',
          type: 'success'
        })
        router.push({
          name: 'layoutCustomer'        
        })
      }
    }

    const breadcrumb = [{
      text: 'Customer',
      to: {
        name: 'layoutCustomer'
      }
    }, {
      text: 'New Customer'
    }]

    return {
      data,
      loading,
      createCustomer,
      breadcrumb,
      refForm
    }
  }
})
</script>